<template>
    <button class="icon-button-box i-location" :disabled="props.disabled" @click.stop="click">
        <i>
            <ShareAltOutlined v-if="props.icon === 'sharing'" /><!--  -->
            <FormOutlined v-if="props.icon === 'record'" /><!--  -->
            <MonitorOutlined v-if="props.icon === 'search'" /><!--  -->
            <AlertOutlined v-if="props.icon === 'alert'" /><!--  -->
        </i>
        <span>{{buttonText}}</span>
    </button>
</template>
<script setup>
    import { ref,watch,defineEmits,defineProps } from 'vue'
    import { ShareAltOutlined,FormOutlined,MonitorOutlined,AlertOutlined } from '@ant-design/icons-vue'
    const props = defineProps({
        icon : {
            type : String,
            default : ()=>{
                return 'sharing' // [record,search,sharing,alert]
            }
        },
        text : {
            type : String,
            default : ()=>{
                return null
            }
        },
        disabled : {
            type : Boolean,
            default : ()=>{
                return false;
            }
        }
    })
    const emit = defineEmits(['click']);
    const textGroup = [{text:"",icon:"sharing"},{text:"",icon:"record"},{text:"",icon:"search"},{text:"",icon:"alert"}]
    const buttonText = ref("");
    const click = ()=>{
        emit("click",props.icon);
    }
    watch(()=>props.icon,()=>{
        if(!props.text){
            const t = textGroup.find(a=>a.icon === props.icon);
            buttonText.value = t.text;
        }
    },{immediate:true})
</script>
<style type="scss" scoped>
    .icon-button-box{
        display:inline-flex;
        flex-direction:column;
        align-items:center;
        width:150rem;
        padding:10rem;
        border-radius:20rem;
        outline:none;
        border:1rem solid #ededed;
        &> i{
            font-size:40rem;
        }
        &> span{
            font-size:16rem;
        }
        &[disabled]{
            &> i, &> span{
                color:#ddd;
            }
        }
    }
</style>